export function GetSpecifications(state) {
    return state.specifications.specificationsData;
}

export function GetSpecificationsTotalCount(state) {
    return state.specifications.totalCount;
}

export function GetSpecification(state) {
    return state.specifications.specificationData;
}

export function GetEnableValues(state) {
    return state.specifications.enabled_values;
}

export function GetDisableValues(state) {
    return state.specifications.disabled_values;
}

export function GetSearchedSpecifications(state) {
    return state.specifications.searchSpecifications;
}