import { useDispatch, useSelector } from "react-redux";
import ReactSwitch from "react-switch";
// State
import { TranslationType, TypesActions, TypeSpecificationType } from "../../../../../Redux/Admin/Types/TypesReducer";
import { GetAccessToken } from "../../../../../Redux/Auth/AuthSelector";
// Thunks
import { switchChangeThunk } from "../../../../../Redux/Admin/Types/TypesThunk";
// Styles
import styles from './ActiveTypeSpecifications.module.css';


export const ActiveTypeSpecifications = (activeSpecifications: any) => {
    const dispatch = useDispatch();

    // Local store
    const access_token = useSelector(GetAccessToken);

    return (
        <>
            {activeSpecifications
                ? activeSpecifications.activeSpecifications.map((activeTypeSpecification: TypeSpecificationType) => {
                    return (
                        <div className={styles.active_specifications_block}>
                            <div>Id: {activeTypeSpecification.id}</div>
                            <div>Absolute: {activeTypeSpecification.absolute_name}</div>
                            <ReactSwitch 
                                checked={true}
                                onChange={() => {
                                    dispatch(switchChangeThunk(activeTypeSpecification.type_spec_id, activeTypeSpecification.deleted, access_token));
                                    dispatch(TypesActions.switchTrueToFalseSpecification(activeTypeSpecification));
                                }}>
                            </ReactSwitch>
                            <div>Type_spec_id: {activeTypeSpecification.id}</div>
                            {activeTypeSpecification.spec_translations
                                .filter((translation: TranslationType) => translation.language_id === 1)
                                .map((translation: TranslationType) => <div>{translation.name}</div>)
                            }
                            {activeTypeSpecification.spec_translations
                                .filter((translation: TranslationType) => translation.language_id === 2)
                                .map((translation: TranslationType) => <div>{translation.name}</div>)
                            }
                        </div>
                    );
                })
                : null
            }
        </>
    )
}