import { useDispatch, useSelector } from 'react-redux';
import ReactSwitch from 'react-switch';
// Store
import { TranslationType, TypesActions, TypeSpecificationType } from '../../../../../Redux/Admin/Types/TypesReducer';
import { GetAccessToken } from '../../../../../Redux/Auth/AuthSelector';
// Thunks
import { switchChangeThunk } from '../../../../../Redux/Admin/Types/TypesThunk';


export const DeletedTypeSpecifications = (props: any) => {
    const dispatch = useDispatch();
    const access_token = useSelector(GetAccessToken);
    
    return (
        <>
            {props.deleteSpecifications.map((deleteTypeSpecification: TypeSpecificationType) => {
                return (
                    <div>
                        <div>Id: {deleteTypeSpecification.id}</div>
                        <div>Absolute: {deleteTypeSpecification.absolute_name}</div>
                        <ReactSwitch
                            checked={false}
                            onChange={() => {
                                dispatch(switchChangeThunk(deleteTypeSpecification.type_spec_id, deleteTypeSpecification.deleted, access_token));
                                dispatch(TypesActions.switchFalseToTrueSpecification(deleteTypeSpecification));
                            }}>
                        </ReactSwitch>
                        <div>Id: {deleteTypeSpecification.id}</div>
                        {deleteTypeSpecification.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 1)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                        {deleteTypeSpecification.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 2)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                    </div>
                );
            })}
        </>
    );
}