import { AxiosResponse } from 'axios';
import { api } from '../../../api';
import { Thunk } from '../../types';
import { LocationActionsType, LocationsActions } from './LocationsReducer';


export const getMainLocationsThunk =
    (setIs_loading: (bool: boolean) => void, token: string): Thunk<LocationActionsType> =>
        async dispatch => {
            try {
                if (token === "") return;
                const response: AxiosResponse<GetResposeType> = await api.getApi({
                    url: `admin/main-locations`,
                    token: token,
                    withCredentials: true,
                    contentType: false
                });
                dispatch(LocationsActions.addMainLocations(response.data.rows));
                setIs_loading(false);
            } catch (error: any) {
                console.log(error.response.message);
            }
        };


export const addMainLocationsThunk =
    (data: any, token: string): Thunk<LocationActionsType> =>
        async dispatch => {
            try {
                const response: AxiosResponse<AddResposeType> = await api.postApi({ url: `admin/add-main-location`, token: token, withCredentials: true, params: data });
                dispatch(LocationsActions.addNewMainLocations(response.data.rows));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

        
export const getRegionLocationsThunk =
    (id: number, access_token: string): Thunk<LocationActionsType> =>
        async dispatch => {
            try {
                const response: AxiosResponse<GetResposeType> = await api.getApi({
                    url: `admin/region-locations/${id}`,
                    token: access_token,
                    withCredentials: true,
                    contentType: false,
                });
                dispatch(LocationsActions.addRegionLocations(id, response.data.rows));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };


export const addRegionLocationsThunk =
    (data: any, token: string): Thunk<LocationActionsType> =>
        async dispatch => {
            try {
                const response: AxiosResponse<AddResposeType> = await api.postApi({
                    url: `admin/add-location`,
                    token: token,
                    withCredentials: true,
                    params: data
                });
                debugger;
                dispatch(LocationsActions.addNewRegionLocations(response.data.rows));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };



// Types
type GetResposeType = {
    rows: {id: number; name: string}[];
};

type AddResposeType = {
    rows: {id: number; name: string};
};