import { useEffect } from "react";
import Modal from "react-modal";
import Switch from "react-switch";
import { useDispatch, useSelector } from "react-redux";
// Thunks
import { getSpecificationThunk, getSpecificationValueThunk } from "../../../Redux/Admin/Specifications/SpecificatiosThunk";
// Components
import { GetDisableValues, GetEnableValues, GetSpecification } from "../../../Redux/Admin/Specifications/SpecificationsSelector";
import { Header } from "../../Header/Header";
import { Exit } from "../../Exit/Exit";
// State
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
// Actions
import { SpecificationsActions } from "../../../Redux/Admin/Specifications/SpecificationsReducer";
// Styles
import styles from './SpecificationEditModal.module.css';

// Types
type SpecificationValuesType = {
    id: number;
    absolute_value: string;
    enable: string;
    value_translations: Array<{language_id: number, name: string}> | null;
};

type PropsType = {
    isModalOpen: boolean;
    changeModalOpen: (bool: boolean) => void;
    id: number
};

export const SpecificationEditModal = (props: PropsType) => {
    const dispatch = useDispatch();

    // Local state
    const access_token = useSelector(GetAccessToken);
    const specification = useSelector(GetSpecification);
    let enabledValues = useSelector(GetEnableValues);
    let disabledValues = useSelector(GetDisableValues);




    if (Object.keys(specification).length !== 0) {
        debugger;
        enabledValues = enabledValues.map((value: SpecificationValuesType) => {
            let turkmenTranslation;
            let russianTranslation;
            if (value.value_translations !== null) {
                for (let i = 0; i < value.value_translations.length; i++) {
                    if (value.value_translations[i].language_id === 1) turkmenTranslation = value.value_translations[i].name;
                }
                for (let i = 0; i < value.value_translations.length; i++) {
                    if (value.value_translations[i].language_id === 2) russianTranslation = value.value_translations[i].name;
                }
            }
            
            return (
                <div className='grid grid-cols-5 justify-items-center mb-4 mt-4'>
                    <div>{value.id}</div>
                    <div>{value.absolute_value}</div>
                    {(value.value_translations)
                        ? <div>{turkmenTranslation}</div>
                        : <div>Ýok</div>
                    }
                    {(value.value_translations) 
                        ? <div>{russianTranslation}</div> 
                        : <div>Ýok</div>
                    }
                    <Switch onChange={() => {
                        dispatch(getSpecificationValueThunk(access_token, value.id, false));
                        dispatch(SpecificationsActions.switchEnabledToDisabledSpecification(value));
                    }} checked={true}/>
                </div>
            );
        })
        disabledValues = disabledValues.map((value: SpecificationValuesType) => {
            let turkmenTranslation;
            let russianTranslation;
            if (value.value_translations !== null) {
                for (let i = 0; i < value.value_translations.length; i++) {
                    if (value.value_translations[i].language_id === 1) turkmenTranslation = value.value_translations[i].name;
                }
                for (let i = 0; i < value.value_translations.length; i++) {
                    if (value.value_translations[i].language_id === 2) russianTranslation = value.value_translations[i].name;
                }
            }
            
            return (
                <div className='grid grid-cols-5 justify-items-center mb-4 mt-4'>
                    <div>{value.id}</div>
                    <div>{value.absolute_value}</div>
                    {(value.value_translations) 
                        ? <div>{turkmenTranslation}</div>
                        : <div>Ýok</div>
                    }
                    {(value.value_translations) 
                        ? <div>{russianTranslation}</div> 
                        : <div>Ýok</div>
                    }
                    <Switch onChange={() => {
                        dispatch(getSpecificationValueThunk(access_token, value.id, true));
                        dispatch(SpecificationsActions.switchDisabledToEnabledSpecification(value));
                    }}
                    className='cursor-pointer'
                    checked={false}/>
                </div>
            );
        }
        )}

    useEffect(() => {
        dispatch(getSpecificationThunk(access_token, props.id));
    }, [access_token, dispatch, props.id]);

    
    let headerItems;

    if (Object.keys(specification).length !== 0) {
        let turkmenTranslation;
        let russianTranslation;
        for (let i = 0; i < specification.translations.length; i++) {
            if (specification.translations[i].language_id === 1) turkmenTranslation = specification.translations[i].name;
        }
        for (let i = 0; i < specification.translations.length; i++) {
            if (specification.translations[i].language_id === 2) russianTranslation = specification.translations[i].name;
        }
        headerItems = [
            `ID: ${specification.id}`, 
            `Absolut ady: ${specification.absolute_name}`, 
            `Türkmençe: ${turkmenTranslation}`,
            `Rusça: ${russianTranslation}`,
            `Çalşmak`
        ];
    };


    return (
        <Modal className={`${styles.main_block}`} isOpen={props.isModalOpen}>
             <div className={`${styles.content_block} relative top-0 left-0`}>
                <Exit changeModalOpen={() => props.changeModalOpen(false)} />
                {(Object.keys(specification).length !== 0) ? <Header headerItems={headerItems}/> : null}
                {enabledValues}
                <hr />
                {disabledValues}
            </div>
        </Modal>
    );
}