import { useDispatch } from "react-redux";
import { TranslationType, TypesActions, UnusedSpecType } from "../../../../../../Redux/Admin/Types/TypesReducer";
import styles from './UnusedSpecs.module.css';

// Types
type PropsType = {
    unusedSpecs: Array<UnusedSpecType>;
};


export const UnusedSpecs = (props: PropsType) => {
    const dispatch = useDispatch();

    return (
        <div className={styles.main_block}>
            {props.unusedSpecs.map((unusedSpec: UnusedSpecType) => {
                return (
                    <>
                        <div>Id: {unusedSpec.spec_id}</div>
                        <div>Абсолютное название: {unusedSpec.absolute_name}</div>
                        {unusedSpec.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 1)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                        {unusedSpec.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 2)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                        <button onClick={() => dispatch(TypesActions.changeUnusedToWannaUse(unusedSpec))}>+</button>
                    </>
                );
            })}
        </div>
    );
};