import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AddMainLocationModal } from "../../../Components/Modal/AddMainLocationModal/AddMainLocationModal";
import { AddRegionsModal } from "../../../Components/Modal/AddRegionsModal/AddRegionsModal";
import { Sidebar } from "../../../Components/Sidebar/Sidebar";
import { GetMainLocations, GetRegionLocations } from "../../../Redux/Admin/Locations/LocationsSelector";
import { getMainLocationsThunk } from "../../../Redux/Admin/Locations/LocationsThunk";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
import styles from './Locations.module.css';
import packmanLoader from '../../../Images/packman.gif';
import { Button } from "../../../Components/Buttons/Button";

export const Locations = () => {
    const access_token = useSelector(GetAccessToken);
    const dispatch = useDispatch();
    const [is_loading, setIs_loading] = useState(false);
    const mainLocations = useSelector(GetMainLocations);
    const regionLocations = useSelector(GetRegionLocations);
    console.log(regionLocations);
    if (regionLocations.lenght === 3) {
        regionLocations[1].regions.push({id: 10, name: 'PEOR'})
    }
    console.log(regionLocations);

    const [add_region_modal_shown, setAdd_region_modal_shown] = useState(false);
    const [clicked_id, setClicked_id] = useState(0);
    
    let headerItems: any = [];

    useEffect(() => {
        setIs_loading(true);
        dispatch(getMainLocationsThunk(setIs_loading, access_token));
    }, [access_token, dispatch]);
    const [modal_shown, setModal_shown] = useState(false);
    if (mainLocations !== undefined) { 
        mainLocations.map((mainLocation: any) => headerItems.push(mainLocation.name));
    }

    return (
        <div className={styles.main_block}>
            {is_loading
            ? <img className={styles.packman_loader} alt='Loader' src={packmanLoader}/>
            : <div className={styles.content_block}>
                {clicked_id ? <AddRegionsModal id={clicked_id} isModalOpen={add_region_modal_shown} changeModalOpen={setAdd_region_modal_shown}/> : null} 
                {mainLocations.map((mainLocation: any) => (
                    <div onClick={() => {setAdd_region_modal_shown(true); setClicked_id(mainLocation.id)}} className={styles.main_location}>{mainLocation.name}</div>
                ))}
                <AddMainLocationModal isModalOpen={modal_shown} changeModalOpen={setModal_shown}/>
                <Button text='Добавить новую локацию' color='yellow' fn={() => {setModal_shown(true)}}/>
               </div>
            }
        </div>
    );
};