import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import Modal from "react-modal"
import { useDispatch, useSelector } from "react-redux";
import { GetRegionLocations } from "../../../Redux/Admin/Locations/LocationsSelector";
import { addRegionLocationsThunk, getRegionLocationsThunk } from "../../../Redux/Admin/Locations/LocationsThunk";
import { GetAccessToken } from "../../../Redux/Auth/AuthSelector";
import { Exit } from "../../Exit/Exit";
import styles from './AddRegionsModal.module.css';

export const AddRegionsModal = (props: {changeModalOpen: (bool: boolean) => void, isModalOpen: boolean, id: number}) => {
    debugger;
    const access_token = useSelector(GetAccessToken);
    const regions = useSelector(GetRegionLocations);
    
    const [modal_shown, setModal_shown] = useState(false);

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getRegionLocationsThunk(props.id, access_token))
    }, [access_token, props.id, dispatch]);
    const {handleSubmit, register} = useForm();
    
    const onFormSubmit = (data: any) => {
        const preparedData = {
            absolute_name: data.Absolute_name,
            main_location_id: props.id,
            translations: [
                {lang_id: 1, name: data.Turkmen_name},
                {lang_id: 2, name: data.Russian_name}
            ]
        };
        dispatch(addRegionLocationsThunk(preparedData, access_token));
    }

    return (
        <Modal isOpen={props.isModalOpen}>
            <div className={`${styles.content_block}`}>
                {regions.map((region: {name: string; id: number}) => {
                    return (
                        <div>{region.name}</div>
                    );
                })}
                <Modal isOpen={modal_shown}>
                    <form className='relative top-0 left-0' onSubmit={handleSubmit(onFormSubmit)}>
                        <Exit changeModalOpen={props.changeModalOpen} />
                        <input
                            placeholder='Absolute ady'
                            {...register('Absolute_name', {required: true}) }
                        />
                        <input 
                            placeholder='Türkmençe'
                            {...register('Turkmen_name', {required: true}) }
                        />
                        <input 
                            placeholder='Rusça'
                            {...register('Russian_name', {required: true})}
                        />
                        <button>Submit</button>
                        <button onClick={() => {setModal_shown(false)}}>Çykmak</button>

                    </form>
                </Modal>
                <button onClick={() => {setModal_shown(true)}} className={styles.add_region_button}>Region goşmak</button>
                <button onClick={() => {props.changeModalOpen(false)}}>Çykmak</button>
            </div>
        </Modal>
    );
}

