import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { searchOperatorThunk } from '../../../Redux/Admin/Operators/OperatorsThunk';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import { CreateOperatorModal } from '../../Modal/CreateOperatorModal/CreateOperatorModal';
import useTimeout from '../../useTimeout/useTimeout';
import styles from './SearchOperators.module.css';


export const SearchOperators = () => {

    const access_token = useSelector(GetAccessToken);
    const [add_operator_modal_shown, setAdd_operator_modal_shown] = useState(false);
    const dispatch = useDispatch();
    const [name_input_state, setName_input_state] = useState('');
    const [phone_input_state, setPhone_input_state] = useState('');
    const handleSearch = useTimeout({ time: 1500, action: (value: any) => dispatch(searchOperatorThunk(value.name, value.phone, access_token))});
    

    return (
        <>
        <CreateOperatorModal isModalShown={add_operator_modal_shown} setIsModalShown={setAdd_operator_modal_shown}/>
        <div className={styles.main_block}>
            <div className={styles.content_block}>

                <div className={styles.search_input_block}>
                    <input 
                        className={styles.search_input_name} 
                        placeholder='ady'
                        onChange={
                            (e) => {                               
                                setName_input_state(e.target.value);
                                handleSearch({name: e.target.value, phone: phone_input_state});
                            }
                        }
                    />
                </div>
                <div  className={styles.search_input_block}>
                    <input 
                        className={styles.search_input_passoword}
                        placeholder='telefon nomery'
                        onChange={
                            (e) => {
                                setPhone_input_state(e.target.value);
                                handleSearch({name: name_input_state, phone: e.target.value});
                            }
                        }
                    />
                </div>
                <button onClick={() => setAdd_operator_modal_shown(true)} className={styles.add_operator_button}>Add operator</button>
            </div>
        </div>
        </>
    );
};