import { Link } from 'react-router-dom';
import styles from './Sidebar.module.css';
import {useHistory} from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { GetRoleId } from '../../Redux/Auth/AuthSelector';
import { AuthActions } from '../../Redux/Auth/AuthReducer';
import Cookies from 'js-cookie';

export const Sidebar = () => {
    const history = useHistory();
    const location = history.location.pathname;
    const role_id = useSelector(GetRoleId);
    const dispatch = useDispatch();

    const onLogoutClick = () => {
        history.push('/auth');
        Cookies.remove('refresh_token');
        dispatch(AuthActions.logout())
    }

    return (
        <div className={styles.main_block}>
            {(role_id === 1) 
            ?
                <>
                    <Link to='/admin/operators' className={`${(location === '/admin/operators') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Операторы</Link>
                    <Link to='/admin/specifications' className={`${(location === '/admin/specifications') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Спецификации</Link>
                    <Link to='/admin/types' className={`${(location === '/admin/types') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Типы</Link>
                    <Link to='/admin/locations' className={`${(location === '/admin/locations') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Локации</Link>
                    <Link to='/admin/pictures' className={`${(location === '/admin/pictures') ? 'bg-yellow-400' : ''} ${styles.links_initial}`}>Картинки</Link>
                </>
            :   
                <>
                    <Link className={`${(location === 'operator/unapproved') ? 'bg-yellow-400' : ''} ${styles.links_initial}`} to='/operator/unapproved'>Ожидающие</Link>
                    <Link className={`${(location === 'operator/vip') ? 'bg-yellow-400' : ''} ${styles.links_initial}`} to='/operator/vip'>VIP</Link>
                    
                </>
            }
            <div onClick={onLogoutClick} className={styles.logout}>Выйти</div>
        </div>
    );
};