import { useEffect, useState } from "react";
import ReactModal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
// State
import { GetAccessToken } from "../../../../../Redux/Auth/AuthSelector";
// Thunk
import { getUnusedSpecifications } from "../../../../../Redux/Admin/Types/TypesThunk";
// Styles
import styles from './AddSpecificationToType.module.css';
import { Exit } from "../../../../../Components/Exit/Exit";
import { GetUnusedSpecs, GetWannaUseSpecs } from "../../../../../Redux/Admin/Types/TypesSelector";
import { UnusedSpecs } from "./UnusedSpecs/UnusedSpecs";
import { WannaUseSpecs } from "./WannaUseSpecs/WannaUseSpecs";
import { AddSpecificationModal } from "../../../../../Components/Modal/AddSpecificationModal/AddSpecificationModal";


type PropsType = {
    isModalOpen: boolean;
    changeModalOpen: (boolValue: boolean) => void;
    id: number;
};


export const AddSpecificationToType = (props: PropsType) => {
    const dispatch = useDispatch();

    // Local store
    const [create_specification_modal_shown, set_create_specification_modal_shown] = useState(false); 

    // Global state
    const access_token = useSelector(GetAccessToken);
    const unusedSpecs = useSelector(GetUnusedSpecs);
    const wannaUseSpecs = useSelector(GetWannaUseSpecs);

    // Initial actions
    useEffect(() => {
        debugger;
        dispatch(getUnusedSpecifications(props.id, access_token));
    }, [access_token, dispatch]) 


    return (
        <ReactModal isOpen={props.isModalOpen}>
            <div className='relative'>
                <Exit changeModalOpen={props.changeModalOpen} />
                {wannaUseSpecs
                    ? <WannaUseSpecs wannaUseSpecs={wannaUseSpecs} changeModalShown={props.changeModalOpen} id={props.id}/>
                    : null
                }
                <hr />
                {unusedSpecs
                    ? <UnusedSpecs unusedSpecs={unusedSpecs}/>
                    : null
                }
                {create_specification_modal_shown
                    ? <AddSpecificationModal isModalOpen={create_specification_modal_shown} changeModalOpen={set_create_specification_modal_shown} />
                    : null
                }
                <button onClick={() => {set_create_specification_modal_shown(true)}}>Создать спецификацию</button>
            </div>
        </ReactModal>
    );
}