import { useForm } from 'react-hook-form';
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { addMainLocationsThunk } from '../../../Redux/Admin/Locations/LocationsThunk';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
import {  Button } from '../../Buttons/Button';
import { Exit } from '../../Exit/Exit';
import styles from './AddMainLocationModal.module.css';

export const AddMainLocationModal = (props: {changeModalOpen: (bool: boolean) => void, isModalOpen: boolean}) => {
    const {handleSubmit, register} = useForm();
    const access_token = useSelector(GetAccessToken);
    const dispatch = useDispatch();
    const onFormSubmit = (data: any) => {
        const preparedData = {
            absolute_name: data.Absolute_name,
            translations: [
                {lang_id: 1, name: data.Turkmen_name},
                {lang_id: 2, name: data.Russian_name}
            ]
        };
        dispatch(addMainLocationsThunk(preparedData, access_token));
    }

    return (
        <Modal isOpen={props.isModalOpen}>
            <form className={`${styles.form_block} relative top-0 left-0`} onSubmit={handleSubmit(onFormSubmit)}>
                <Exit changeModalOpen={props.changeModalOpen}/>
                <input
                    placeholder='Absolute ady'
                    {...register('Absolute_name', {required: true}) }
                />
                <input 
                    placeholder='Türkmençe'
                    {...register('Turkmen_name', {required: true}) }
                />
                <input 
                    placeholder='Rusça'
                    {...register('Russian_name', {required: true})}
                />
                <Button text='Добавить локацию' color='blue' />
                <Button text='Выйти' color='red' fn={() => props.changeModalOpen(false)} />
            </form>
        </Modal>
    );
}