import { useDispatch, useSelector } from 'react-redux';
import styles from './SearchSpecifications.module.css';
import { searchSpecificationsThunk } from '../../../../Redux/Admin/Specifications/SpecificatiosThunk';
import { GetAccessToken } from '../../../../Redux/Auth/AuthSelector';
import useTimeout from '../../../useTimeout/useTimeout';
import { AddSpecificationModal } from '../../../Modal/AddSpecificationModal/AddSpecificationModal';

export const SearchSpecifications = (props: {changeModalOpen: (bool: boolean) => void, isModalOpen: boolean}) => {
    const dispatch = useDispatch();
    const access_token = useSelector(GetAccessToken);


    const handleSearch = useTimeout({ time: 1500, action: (value: string) => dispatch(searchSpecificationsThunk(value, access_token))});
    
    return (
        <div className={styles.main_block}>
            <div className={styles.content_block}> 
               <AddSpecificationModal changeModalOpen={props.changeModalOpen} isModalOpen={props.isModalOpen}/>
                <input placeholder='ady' onChange={(e) => handleSearch(e.target.value)}/>
                <button onClick={() => props.changeModalOpen(true)} className={styles.add_specification_button}>Spesifikasiýa goşmak</button>
            </div>
        </div>
    );
};