import { api } from '../../../api';
import { TId_Fn_Actn } from '../../types';
import { TypesActions } from './TypesReducer';


export const getTypesThunk = (token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response: any = await api.getApi({url: `admin/all-types`, token: token, withCredentials: true, contentType: false});
            dispatch(TypesActions.addTypes(response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const getTypeThunk = ({id, changeIsLoading, access_token}: TId_Fn_Actn) => {
    return async (dispatch: any) => {
        try {
            if (access_token === "") return;
            const response: any = await api.getApi({url: `admin/type/${id}`, token: access_token, withCredentials: true, contentType: false});
            dispatch(TypesActions.addType(response.data));
            changeIsLoading(false);
        } catch (error: any) {
            console.log(error);
            
            console.log(error.response.message);
        }
    };
};

export const switchChangeThunk = (id: number, boolValue: boolean, access_token: string) => {
    return async (dispatch: any) => {
        try {
            if (access_token === "") return;
            const response = await api.postApi({url: `admin/activation-type-specification/${id}`, token: access_token, withCredentials: true, params: {bool: boolValue}});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const uploadImageThunk = (id: number, formData: any , token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.uploadPhoto({url: `admin/add-image-to-type/${id}`, token: token, withCredentials: true, formData});
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const getUnusedSpecifications = (id: number, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.getApi({url: `admin/not-contained-spec/${id}`, token: token, withCredentials: true, contentType: false});
            dispatch(TypesActions.addUnusedSpecToType(response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const addSpecificationsToTypeThunk = (spec: any, type_id: number, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.postApi({url: `admin/add-specifications-to-type/${type_id}`, token: token, withCredentials: true, params: {specifications: spec} });
            debugger;
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};