import { AxiosResponse } from 'axios';
import { api } from '../../../api';
import { Thunk } from '../../types';
import { TypesActions } from '../Types/TypesReducer';
import { SpecificationsActions, SpecificationsActionsType, ValuesType } from './SpecificationsReducer';


export const getAllSpecificationsThunk =
    (setIs_loading: (bool: boolean) => void, token: string): Thunk<SpecificationsActionsType> =>
        async dispatch => {
            try {
                if (token === "") return;
                const response: AxiosResponse<SpecificationsResponseType> = await api.getApi({
                    url: `admin/get-all-specifications`,
                    token: token,
                    withCredentials: true,
                    contentType: false
                });
                dispatch(SpecificationsActions.addSpecifications(response.data.rows[0].specifications));
                setIs_loading(false);
            } catch (error: any) {
                console.log(error.response.message);
            }
        };


export const searchSpecificationsThunk =
    (name: string, token: string): Thunk<SpecificationsActionsType> =>
        async dispatch => {
            try {
                if (token === "") return;
                const response: AxiosResponse<SpecificationsResponseType> = await api.getApi({
                    url: `admin/get-all-specifications?name=${name}`,
                    token: token,
                    withCredentials: true,
                    contentType: false
                });
                dispatch(SpecificationsActions.addSearchedSpecifications(response.data.rows[0].specifications))
            } catch (error: any) {
                console.log(error.response.message);
            }
        };


export const getSpecificationThunk =
    (token: string, id: number): Thunk<SpecificationsActionsType> =>
        async dispatch => {
            debugger;
            try {
                if (token === "" || id === 0) return;
                const response: AxiosResponse<SpecificationResponseType> = await api.getApi({
                    url: `admin/get-specification/${id}`,
                    token: token,
                    withCredentials: true,
                    contentType: false
                });
                dispatch(SpecificationsActions.addSpecification(response.data[0]));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

export const getSpecificationValueThunk =
    (token: string, id: number, bool: boolean): Thunk<SpecificationsActionsType> =>
        async dispatch => {
            try {
                if (token === "" || id === 0) return;
                await api.postApi({ url: `admin/disable-enable-spec-val/${id}`, token: token, withCredentials: true, params: { bool: bool } });
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

export const createSpecificationThunk =
    (data: any, token: string): Thunk<SpecificationsActionsType> =>
        async dispatch => {
            try {
                if (token === "") return;
                const response = await api.postApi({ url: `admin/add-specification/`, token: token, withCredentials: true, params: data });
                debugger;
                const preparedDataToDispath = {
                    spec_id: response.data.rows.id,
                    absolute_name: response.data.rows.absolute_name,
                    spec_translations: [
                        { name: data.translations[0].name, language_id: data.translations[0].lang_id },
                        { name: data.translations[1].name, language_id: data.translations[1].lang_id }
                    ],
                };
                // dispatch(TypesActions.addCreatedSpecToUnused(preparedDataToDispath));
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

export const changeSpecificationActivationThunk =
    (id: number, bool: boolean, changeLoading: (id: number) => void, token: string): Thunk<SpecificationsActionsType> =>
        async (dispatch: any) => {
            try {
                if (token === "") return;
                await api.postApi({ url: `admin/activation-of-specification/${id}`, token: token, withCredentials: true, params: { bool: bool } });
                dispatch(SpecificationsActions.changeSpecificationActivation(id, bool));
                changeLoading(0);
            } catch (error: any) {
                console.log(error.response.message);
            }
        };

export const changeSearchedSpecificationActivationThunk = (id: number, bool: boolean, changeLoading: (id: number) => void, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            await api.postApi({ url: `admin/activation-of-specification/${id}`, token: token, withCredentials: true, params: { bool: bool } });
            dispatch(SpecificationsActions.changeSearchedSpecificationActivation(id, bool));
            changeLoading(0);
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};


// Types
type SpecificationsResponseType = {
    rows: [{
        count: string;
        specifications: [{
            id: number;
            absolute_name: string;
            is_multiple: boolean;
            is_required: boolean;
            is_active: boolean;
            translations: [{
                language_id: number;
                name: string;
            }]
        }]
    }]
}

type SpecificationResponseType = [{
    id: number;
    absolute_name: string;
    translations: {
        language_id: number;
        name: string;
    }[];
    enabled_values: null | ValuesType[];
    disabled_values: null | ValuesType[];
}];