import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ReactModal from 'react-modal';
// Components
import { Exit } from '../../Exit/Exit';
// State
import { GetLocationImages } from '../../../Redux/Admin/Pictures/ImagesSelector';
import { GetAccessToken } from '../../../Redux/Auth/AuthSelector';
// Types
import { LocationImagesType } from '../../../Redux/Admin/Pictures/ImagesReducer';
// Thunks
import { getImagesLocationThunk, uploadImageThunk } from '../../../Redux/Admin/Pictures/ImagesThunk';
// Styles
import styles from './ChangeImagesModal.module.css';


// Types
type PropsType = {
    isModalOpen: boolean;
    changeModalOpen: (bool: boolean) => void;
    id: number
};


export const ChangeImagesModal = (props: PropsType) => {
    const dispatch = useDispatch();

    // State
    const access_token = useSelector(GetAccessToken);
    const images = useSelector(GetLocationImages);
    const [file, setFile] = useState<any>();
    
    // Render element
    let imagesRender;
    if (images) {
        imagesRender = images.map((image: LocationImagesType) => {
            return (
                <img src={`${process.env.REACT_APP_BASE_URL}/${image.destination}`}
                    className={styles.images}
                />
            );
        });
    };

    // Functions
    const UploadContent = (event: any) => {
        event.preventDefault();
        if (event.target.files[0]) {
            setFile(event.target.files[0]);
        }
    };

    const OnSumbit = (event: any) => {
        const formData = new FormData();
        formData.append('picture', file);
        dispatch(uploadImageThunk(props.id, formData, access_token));
        props.changeModalOpen(false);
    }

    // Initial actions
    useEffect(() => {
        dispatch(getImagesLocationThunk(props.id, access_token));
    }, [access_token, dispatch, props.isModalOpen]);


    return (
        <ReactModal isOpen={props.isModalOpen}>
            <Exit changeModalOpen={() => props.changeModalOpen(false)} />
            <div className={styles.images_block}>{imagesRender}</div>
            <input
                accept='image/png, image/jpeg'
                type="file"
                onChange={UploadContent}
            />
            <button onClick={OnSumbit}>
                Сохранить и закрыть
            </button>
        </ReactModal>
    )
}