import { FC, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Route, Switch } from 'react-router';
import { Sidebar } from '../../Components/Sidebar/Sidebar';
import { loadAdminThunk } from '../../Redux/Auth/AuthThunks';
import {AdminRoutes} from './routes';
import styles from './AdminIndex.module.css';

export default function AdminIndex() {
  const dispatch = useDispatch();
  
  useEffect(() => {
    dispatch(loadAdminThunk());
  }, [dispatch]);

  type RouteType = {
    path: string;
    Component: FC;
  }

    return (
      <>
          <div className={styles.main_block}>
            <Sidebar />
            <Switch>
              {AdminRoutes.map(({path, Component}: RouteType) => <Route component={Component} exact key={path} path={path} />)}
            </Switch>
          </div>
      </>
    )
}