import { api } from '../../../api';
import { ImagesActions } from './ImagesReducer';


export const getImagesLocationsThunk = (token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.getApi({url: `admin/page-image-places`, token: token, withCredentials: true, contentType: false});
            dispatch(ImagesActions.addImagesLocations(response.data.rows));
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const getImagesLocationThunk = (id: number, token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.getApi({url: `admin/get-page-images/${id}`, token: token, withCredentials: true, contentType: false});
            dispatch(ImagesActions.addLocationImages(response.data.rows));            
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

export const uploadImageThunk = (id: number, formData: any , token: string) => {
    return async (dispatch: any) => {
        try {
            if (token === "") return;
            const response = await api.uploadPhoto({url: `admin/add-page-image/${id}`, token: token, withCredentials: true, formData});
            console.log(response.data);
            
            debugger;
        } catch (error: any) {
            console.log(error.response.message);
        }
    };
};

