import styles from './Header.module.css';

export const Header = (props: any) => {
    const headerItemsLength = props.headerItems.length;
    return (
        <div className={`grid grid-cols-${headerItemsLength} ${styles.content_header_block}`}>
            {props.headerItems.map((item: string) => {
                return <div>{item}</div>
            })}
        </div>
    );
}