export function GetOperators(state) {
    return state.operators.operators;
}

export function GetSearchedOperators(state) {
    return state.operators.searchedOperators;
}

export function GetOperatorsCount(state) {
    return state.operators.operatorsCount;
}

export function GetOperatorsIsLoading(state) {
    return state.operators.isLoading;
}