// Components
import { Pictures } from "./Images/Images";
import { Locations } from "./Locations/Locations";
import { Operators } from "./Operators/Operators";
import { Specifications } from "./Specifications/Specifications";
import { Types } from "./Types/Types";

// Admin paths
const operators = '/admin/operators';
const specifications = '/admin/specifications';
const types = '/admin/types';
const locations = '/admin/locations';
const pictures = '/admin/pictures';


export const AdminRoutes = [
    {
        path: operators,
        Component: Operators,
    },
    {
        path: specifications,
        Component: Specifications
    },
    {
        path: types,
        Component: Types
    },
    {
        path: locations,
        Component: Locations
    },
    {
        path: pictures,
        Component: Pictures
    }
];