import { useState } from "react";
import { useForm } from "react-hook-form";
import toast from "react-hot-toast";
import { useDispatch, useSelector } from "react-redux";
import { TranslationType, TypesActions, UnusedSpecType } from "../../../../../../Redux/Admin/Types/TypesReducer";
import { addSpecificationsToTypeThunk } from "../../../../../../Redux/Admin/Types/TypesThunk";
import { GetAccessToken } from "../../../../../../Redux/Auth/AuthSelector";
import styles from './WannaUseSpecs.module.css';

// Types
type PropsType = {
    wannaUseSpecs: Array<UnusedSpecType>;
    changeModalShown: (bool: boolean) => void;
    id: number;
};


export const WannaUseSpecs = (props: PropsType) => {
    const dispatch = useDispatch();

    // Global state
    const access_token = useSelector(GetAccessToken);

    // Functions
    const onInputChange = (wannaUseSpec: any, position: number | null) => {
        wannaUseSpec['position'] = position;
    }

    const onSaveClick = () => {
        let preparedData: any = [];
        props.wannaUseSpecs.map((wannaUseSpec: any) => {
            if (wannaUseSpec.position === null) return toast.error('Позиция обязательна');
            else {
                preparedData.push({
                    id: wannaUseSpec.spec_id,
                    position: wannaUseSpec.position,
                });
                dispatch(addSpecificationsToTypeThunk(preparedData, props.id, access_token));
            }
        });
    }


    return (
        <div className={styles.main_block}>
            {props.wannaUseSpecs.map((wannaUseSpec: any) => {
                return (
                    <>
                        <div>Id: {wannaUseSpec.spec_id}</div>
                        <div>Абсолютное название: {wannaUseSpec.absolute_name}</div>
                        {wannaUseSpec.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 1)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                        {wannaUseSpec.spec_translations
                            .filter((translation: TranslationType) => translation.language_id === 2)
                            .map((translation: TranslationType) => <div>{translation.name}</div>)
                        }
                        <input type='number' onChange={(e) => {wannaUseSpec['position'] = e.target.value}} 
                            required
                            className={styles.position_number_input}/>

                        <button onClick={() => {dispatch(TypesActions.changeWannaUseToUnused(wannaUseSpec))}}>-</button>
                    </>
                );
            })}
        <button onClick={() => {props.changeModalShown(false); onSaveClick()}}>Сохранить изменения</button>
        </div>
    );
};